.signupLogin{
    gap: 15px;
    margin: 30px auto;
    border-radius: 3px;
    border: solid #ddd;
    form{
        width: 300px;
    }
    button{
        width: 300px;
    }
    p{
        margin-top: 0;
    }
    a{
        color: #ddd;
    }
    a:hover {
        text-decoration: underline;
    }
}

@media (max-width: 430px) {
    .signupLogin {
        border: none;
    }
}