@media (max-width: 430px) {
  .main-body {
      max-width: 330px;
  }
}
@media screen and (max-width: 330px){
  .main-body{
      max-width: 210px;
      gap: 2px;
      form, button{
          width: 200px;
      }
  }
  .navbar{
    width: 200px;
    button{
      width: 98px;
    }
  }
  .inputWrapper{
    .toggle-password-icon{
      margin-left: 170px;
    }
  }
  .transactions{
    max-width: 200px;
  }
}
@media screen and (max-width: 225px){
  .main-body{
      max-width: 150px;
      form, button{
          width: 100px;
      }
  }
  .navbar{
    width: 100px;
  }
  .inputWrapper{
    .toggle-password-icon{
      margin-left: 75px;
    }
  }
  .transactions{
    max-width: 100px;
  }
}