.transactions{
  margin-top: 10px;
  width: 300px;
  overflow-y: auto;
  padding-right: 2px;
  // Scrollbar width
  &::-webkit-scrollbar {
    width: 8px;
  }
  // Scrollbar track (background)
  &::-webkit-scrollbar-track {
    background: #1e1e2f;
    border-radius: 4px;
  }
  // Scrollbar thumb (the draggable part)
  &::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 4px;
    border: 2px solid #1e1e2f; // optional: adds space around the thumb
  }
  // On hover
  &::-webkit-scrollbar-thumb:hover {
    background-color: #aaa;
  }
  .transaction{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 5px 0;
    border-top: 1px solid #30313d;
    .left {
      flex: 1;
      display: flex;
      flex-direction: column;
      .description {
        word-break: break-word;
      }
      .update-delete-transaction{
        display: flex;
        gap: 5px;
        svg{
          width: 18px;
          height: 18px;
        }
        .delete-span{
          color: #c11;
          font-size: 0.8rem;
        }
        .delete-button{
          border: 1px solid #ddd;
          border-radius: 3px;
          padding-left: 2px;
          padding-right: 2px;
        }
      }
    }
    .right {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      gap: 2px;
      .price-red {
        color: #c11;
      }
      .price-green {
        color: #1c1;
      }
  
      .time {
        font-size: 0.85em;
        color: #999;
      }
    }
  }
  // .transaction:first-child{
  //   border-top: 0;
  // }
}