.pointer{
  cursor: pointer !important;
}
.main-body{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  width: 400px;
  form{
    width: 300px;
  }
  button{
      width: 300px;
  }
}
.navbar{
  display: flex;
  flex-wrap: wrap;
  gap: 3px;
  width: 300px;
  button{
    width: 148px;
  }
}
button, input{
  color: #ddd;
  padding: 10px;
  border-radius: 3px;
  background-color: transparent;
}
button:active {
  transform: scale(0.99);
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.2);
}
button {
  transition: all 0.1s ease-in-out;
}
button{
  border: solid #ddd;
}
input{
  border: 2px solid #30313d;
}
.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  svg {
    width: 50px;
    height: 50px;
  }
}
.navbar-item {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  svg {
    width: 20px;
    height: 20px;
  }
}
.inputWrapper{
  display: flex;
  flex-direction: column;
  .toggle-password-icon{
    position: absolute;
    margin-top: 10px;
    margin-left: 270px;
    width: 18px;
    height: 18px;
  }
  .inputError{
    color: red;
    font-size: 0.8rem;
  }
}
.errorMessage{
  color: red;
  margin: 0;
}