.homePage{
    margin: auto;
    height: 100vh;
    input{
        margin-bottom: 5px;
    }
    form{
        margin-top: 10px;
    }
    .price-and-date{
        display: flex;
        flex-wrap: wrap;
        gap: 7px;
        .price-input{
            width: 146px;
        }
        .date-input{
            width: 147px;
        }
    }
    .description-input{
        width: 300px;
    }
}
@media screen and (max-width: 330px){
    .homePage{
        .description-input{
            width: 200px;
        }
        .price-and-date{
            .price-input{
                width: 96px;
            }
            .date-input{
                width: 97px;
            }
        }
    }
}
@media screen and (max-width: 225px){
    .homePage{
        .description-input{
            width: 100px;
        }
    }
}